/**
 * 
 */
import axios from 'axios'
export default {
  /**
     * 
     */
  addProject(params) {
    let data = Object.assign({}, params, { creator: "" })
    return axios.post("/server/project/addProject", data).then(res => res.data);
  },
  /**
   * 
   */
  searchProject(params) {
    return axios.post("/server/project/searchProject", params).then(res => res.data);
  },
  searchProjectById(id) {
    return axios.get(`/server/project/searchProjectById?id=${id}`).then(res => res.data);
  },
  /**
   * 
   */
  delProject(id) {
    return axios.delete(`/server/project/deleteProject`, { params: { id: id } }).then(res => res.data);
  },
  /**
   * 
   */
  editProject(params) {
    return axios.post(`/server/project/modifyState`, params).then(res => res.data);
  },

  updateProjectConfig(params){
    return axios.post("/server/analysisObject/addLogMapper",params).then(res => res.data);
  },

  updateProjectConfigAndUpdateRule(params){
    return axios.post("/server/analysisObject/saveLogMapper",params).then(res => res.data);
  },

  searchProjectConfig(projectId){
    return axios.get("/server/analysisObject/searchLogMapperByProjectId",{params:{projectId}}).then(res => res.data);

  },
  /**
   * 
   */
  getCollectData(){
    return axios.get('/server/importFile/searchBulkFileMessage' ,{}).then(res =>res.data)
  },
  /**
   * /()
   */
  updateProjectAnalysisMapper(params){
    return axios.post('/server/projectAnalysisMapper/updateProjectAnalysisMapper',params).then(res =>res.data)
  },
  /**
   * /
   */
  updateUsedRuleGroup(params){
    return axios.post('/server/usedRuleGroup/updateUsedRuleGroup',params).then(res =>res.data)
  },

    /**
     * : 
     * @param params
     * @returns {Promise<AxiosResponse<T> | never>}
     */
    selectFilesByPage(params){
    return axios.post('/server/fileRecords/selectFilesByPage',params).then(res =>res.data)
  }


}
