// 
import Vue from 'vue'

// v-might   DOMv-if
Vue.directive('might', {
    inserted: function (el, binding) {
        let mights = JSON.parse(sessionStorage.getItem('btnMights'));
        if (!mights.includes(binding.value)) {
            el.parentNode.removeChild(el);
        }
    }
})

//  v-drag
Vue.directive('drag', {
    inserted: function (el, binding) {
        let index = binding.value.domIndex;
        if (binding.value.bol) {
            let dom = el;
            for(let i = 0;i<index;i++){
                dom = dom.parentNode
            }
            // let dom = el.parentNode.parentNode;
            el.onmousedown = function (ev) {
                // console.log(dom)
                // ev.clientX 
                // console.log(ev.clientX)
                // 
                // console.log(dom.offsetLeft)
                //     top
                let sX = ev.clientX - dom.offsetLeft;
                let sY = ev.clientY - dom.offsetTop;
                document.onmousemove = function (ev) {
                    // domtopleft top
                    let fx = ev.clientX - sX;
                    let fy = ev.clientY - sY;
                    //  top0
                    if (fx < 0) {
                        fx = 0
                    }
                    if (fy < 0) {
                        fy = 0
                    }
                    // console.log(fx)
                    // console.log(fy)
                    dom.style.left = fx + 'px';
                    dom.style.top = fy + 'px';
                }
                document.onmouseup = function () {
                    // 
                    document.onmousemove = null
                }
            }
        }
    }
})