import axios from 'axios'

// 
axios.interceptors.request.use(config => {
    // console.log(config)
    return config
},error => {
    // 
    // console.log(error)
    return Promise.reject(error)
})

// 
axios.interceptors.response.use(res => {
    // console.log(res)
    return res
},error => {
    // 
    // console.log(error)
    return Promise.reject(error)
})