import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 
    isLoading:false, // loading
    // 
    objectData:[],
    // 
    ruleGroupData:[],
    // 
    unitData:[],
    // en,id
    selectUnitEn:"",
    unitId:"",
    objByCurrentUnit:[],
  },
  mutations: {
    // 
    setLoading(state,isLoading){
      state.isLoading = isLoading;
    },
    // 
    setObjectData(state,data){
      state.objectData = data;
    },
    // 
    setRuleGroupData(state,data){
      state.ruleGroupData = data;
    },
    // 
    setUnitData(state,data){
      state.unitData = data;
      //  sessionsession unitsession
      if(sessionStorage.getItem('selectUnitEn')){
        state.selectUnitEn = sessionStorage.getItem('selectUnitEn');
        state.unitId = sessionStorage.getItem('unitId');
      } else {
        state.selectUnitEn = data[0].ename;
        state.unitId = data[0].id;
        sessionStorage.setItem('selectUnitEn',data[0].ename);
        sessionStorage.setItem('unitId',data[0].id);
      }
    },
    // 
    setSelectUnitEn(state,unitEn){
      console.log(unitEn)
      state.selectUnitEn = unitEn;
    },
    // id
    setSelectUnitId(state,unitId){
      state.unitId = unitId;
    },
    // by
    setObjDataByUnit(state,data){
      state.objByCurrentUnit = data;
    }
  },
  actions: {
    // 
    //   mutations
    getObjectData(context,that){
      that.$API.searchObject({}).then(res => {
        if(res.code === 1000){
          context.commit('setObjectData',res.data);
        } else {
          context.commit('setObjectData',[]);
        }
      })
    },
    //   mutations
    getRuleGroupData(context,that){
      that.$API.searchRuleGroup({}).then(res => {
        if(res.code === 1000){
          context.commit('setRuleGroupData',res.data);
        } else {
          context.commit('setRuleGroupData',[]);
        }
      })
    },
    //   mutations
    getUnitData(context,that){
      that.$API.searchUnit({}).then(res => {
        if(res.code === 1000) {
          context.commit('setUnitData',res.data);
        } else {
          context.commit('setUnitData',[]);
        }
      })
    },
    // id
    getObjByUnit(context,that){
      that.$API.searchObjectByUnit(context.state.selectUnitEn).then(res => {
        if(res.code === 1000){
          context.commit('setObjDataByUnit',res.data)
        } else {
          context.commit('setObjDataByUnit',[])
        }
      })
    }
  },
  getters: {
    // vuexcomputer
    objByUnit:state => {
      console.log("",state.objByCurrentUnit)
      let arr = state.objByCurrentUnit.map(item => {
        return {
          moduleCn: item,
          moduleEn: item,
          icon: 'search',
          pathName:"searchLog",
          children: []
        }
      })
      return arr
    }
  },
  modules: {
  }
})
